import React from "react";
import { useState } from "react";
import {Link} from "react-router-dom";
import Datepicker from 'react-datepicker';

function Payment_page(){
    const [message, setMessage] = useState('');
    const [error, setError] = useState(null);
    const [name, setName] = useState('');
    const [error_name, setError_name] = useState(null);
   
    function isValidEmail(email) {
      return /\S+@\S+\.\S+/.test(email);
    }
  
    const handleChangeEmail = event => {
      if (!isValidEmail(event.target.value)) {
        setError('Please enter a valid email address.');
      } else {
        setError(null);
      }
      setMessage(event.target.value);
    }
      
    function isValidName(name) {
      return /^[a-zA-Z]+ [a-zA-Z]+$/.test(name);
    }

    const handleChangeName = event => {
      if (!isValidName(event.target.value)) {
        setError_name('Please enter a valid name.');
      }else {
        setError_name(null);
      }
      setMessage(event.target.value);
    };  
        return(
            <section id ="services">
            <div className="container text-center">
              <h1 className ="title_service">PAYMENT</h1>
              <div className="row bg-light justify-content-md-center">
                <div className="col-md-6">
                    <h2>Email Address</h2>
                    <input type ="email"
                        placeholder="Required"
                        className="form-control"
                        id="message"
                        name="message"
                        value={message}
                        onChange={handleChangeEmail} />
                        {error && <h4 style={{color: "red"}}>{error}</h4>}

                    <h2>Name on Card</h2>
                    {/* <input type ="name"
                        placeholder="Required"
                        className="form-control"
                        id="name"
                        name="name"
                        value={name}
                        onChange={handleChangeName} />
                        {error_name && <h4 style={{color: "red"}}>{error_name}</h4>}  */}
                    <input type="text" className="form-control" placeholder="Required" />
                    <h2>Card Number</h2>
                    <input type="text" className="form-control" placeholder="Credit card number" />
                    <h2>Expiration Date</h2>
                    <input type="text" className="form-control"  placeholder="MM/YYYY" />
                    <h2>CVV</h2>
                    <input type="text" className="form-control" placeholder="3-digits" />
                    <h2>Zip Code</h2>
                    <input type="text" className="form-control" placeholder="Zip Code" />
                    <Link to="/Subscribe_plan" className="btn btn-danger">Cancel</Link>
                    <Link to="/Pay_page" className="btn btn-success">Pay</Link>

                </div>

               
            </div>
            </div>       
        </section>)
        
    }



export default Payment_page;